const optionsKeys = (promptsResolver, optionsId, exclusions) =>
  promptsResolver()
    .find(p => p.id === optionsId).options.map(o => exclusions.includes(o.key) ? '$$_IGNORE_OPTION' : o.key)

const Yes = (key) => responses => responses[key] === 'Yes'
const anyYes = (arg1, arg2, arg3) => Array.isArray(arg1) ? anyYesArray(arg1) : anyOptionYes(arg1, arg2, arg3)

const anyYesArray = (keys) => responses => keys.find(k => responses[k] === 'Yes') !== undefined
const anyOptionYes = (promptsResolver, optionsId, exclusions) =>
  responses => optionsKeys(promptsResolver, optionsId, exclusions).find(k => responses[k] === 'Yes') !== undefined

const hasText = (key) => responses => (!!responses[key] && responses[key].length)

export { Yes, anyYes, hasText, optionsKeys }
